import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import {Link} from 'react-router-dom';
import './OurFeatures.css'; 

function OurFeatures() {
    const imgstyle={
        display: "block",
        width: '100%',
        height: 'auto',
        position: 'relative',
        overflow: 'hidden',
    };

    const parastyle = {
        fontSize: '16px',
        fontWeight: '400',
        lineHeight: '100%',
    }

    return (
        <div>
            <Container>
                <Row className="mb-5">
                    <Col className="d-flex row justify-content-center mt-5 mb-5" xs={12}>
                        Our Features<br/>
                        <div className="h1 d-flex row justify-content-center">Variety of foods</div>
                    </Col>
                    <Col xs={12}>
                        <Row>
                            <Col md={3} className="colmd3 mt-2 d-none d-lg-block">
                                <img src="/v1.png" alt="V1" style={imgstyle}/>
                            </Col>
                            <Col md={3} className="colmd3 mt-3">
                                <h2>Veggies are Forever.</h2>
                                <p style={parastyle}>Get your vegetables delivered home fresh
                                    and wholesome.
                                </p>
                                <Link to="/listing">
                                <Button variant="success">
                                      <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                      Discover Now</p>
                                </Button>{' '}
                                </Link>
                            </Col>
                            <Col md={3} className="colmd3 mt-2  d-none d-lg-block">
                                <img src="/v2.png" alt="V2" style={imgstyle}/>
                            </Col>
                            <Col md={3} className="colmd3 mt-3">
                                <h2>Good for your skin.</h2>
                                <p style={parastyle}>Get your skin glowing with fresh fruits
                                    delivered from the farms.
                                </p>
                                <Link to="/listing">
                                   <Button variant="success">
                                      <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                      Discover Now</p>
                                   </Button>{' '}
                                </Link>   
                            </Col>
                        </Row>

                        <Row>
                            <Col md={3} className="colmd3 mt-3">
                                <h2>Veggie Food Grove.</h2>
                                <p style={parastyle}>Get your fruits and vegetables as specialised
                                   kits.
                                </p>
                                <Link to="/listing">
                                    <Button variant="success">
                                      <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                      Discover Now</p>
                                    </Button>{' '}
                                </Link>    
                            </Col>
                            <Col md={3} className="colmd3 mt-2 d-none d-lg-block">
                                 <img src="/v3.png" alt="V3" style={imgstyle}/>
                            </Col>
                            <Col md={3} className="colmd3 mt-3">
                                <h2>That Comes Close.</h2>
                                <p style={parastyle}>Get your fruits and vegetables as cut or processed
                                       ready to cook.
                                </p>
                                <Link to="/listing">
                                <Button variant="success">
                                      <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                      Discover Now</p>
                                </Button>{' '}
                                </Link>
                            </Col>
                            <Col md={3} className="colmd3 mt-2 d-none d-lg-block">
                               <img src="/v4.png" alt="V4" style={imgstyle}/>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default OurFeatures
