import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import {Link} from 'react-router-dom';

function CardComp(props) {
    return (
                  <Card style={{ width: '16rem' }} className="card mt-sm-2 m-1">
                    <Card.Body className = "d-flex row justify-content-center">
                        <div className = "d-flex row justify-content-center">
                        <img src={props.image} alt={props.name} className="align-self-center" style={{height: '180px',width: 'auto',margin: '0px auto',}}/>
                        </div>
                        <div className = "d-flex column justify-content-center">
                        <Card.Title className="mt-4">{props.name}</Card.Title></div>
                        {/*<Card.Subtitle className="mb-2 text-muted">Card Subtitle</Card.Subtitle>*/}
                        <Card.Text>
                          <span className = "d-flex column justify-content-center"> 
                            <div style={{textDecoration:'line-through',color: 'grey',fontWeight: 'bold',display: 'inline'}}> &#36;{props.oldrate}</div>
                            &nbsp;<div style={{textDecoration:'none',color: '#06C167',fontWeight: 'bold',display: 'inline'}}>&#36;{props.newrate}</div>
                          </span>
                        </Card.Text>
                        {/*<Card.Link href="#">Card Link</Card.Link>*/}
                        <Card.Link href="#" style={{textDecoration: 'none',}}>
                             <div className = "d-flex row justify-content-center">
                                <Link to={props.proute} className = "d-flex row justify-content-center" style={{textDecoration:'none',}}> 
                                   <Button variant="success">
                                       <p style={{padding:'1px 1px',fontSize: '14px',margin: 'auto auto',textDecoration: 'none',}}>
                                       Buy Now</p>
                                   </Button>{' '}
                                </Link>   
                             </div>     
                        </Card.Link>
                    </Card.Body>
                  </Card>            
    )
}

export default CardComp
