import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';

function Benefit() {
    const style={
       fontSize : '20px',
       fontWeight : 400,
       lineHeight : '100%',
    };

    return (
        <div>
           <Container> 
                    <Row className="mt-5 mb-5">
                        <Col sm={12} md={5}>
                            <img src="/benefit.png" alt="benefit" className="img-fluid"/>
                        </Col>
                        <Col sm={12} md={7} className="ps-5 pt-4"> 
                            Our benefit<br/><br/>
                            <h1>The More Healthy food the better</h1> <br/>
                           <div style={style}> 
                            <p style={style}>Be alive with Veggie Food.</p>
                            <p style={style}>It's the place to be.</p>
                            <p style={style}>Non Stop Veggie Food.</p>
                            <p style={style}>The best silk dishes in the town.</p>
                            <p style={style}>Truffles,eggs and pumpkin spice.</p>
                           </div> 
                        </Col>
                    </Row>
           </Container> 
        </div>
    )
}

export default Benefit
