import React from 'react';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import {Link} from 'react-router-dom';
import Container from 'react-bootstrap/Container';

function EndBanner() {
    const style1={
        backgroundImage: 'url(./endBannerd.png)' ,
        backgroundPosition: 'center', /* Center the image */
        backgroundRepeat: 'no-repeat', /* Do not repeat the image */
        backgroundSize: 'cover',
        minHeight: '400px',
    };

    return (
        <div>
            <Container className="d-flex row justify-content-center mt-5 mb-5"  style={style1}>
                <Row className="d-flex">
                    {/* <img src="./endBanner.png" alt="End Banner" className="img-fluid"/> */}
                    <div className="d-flex row justify-content-around" >
                      
                      <div className="d-flex align-self-center justify-content-around" style={{color: 'White',}}>
                        <h1 className="align-self-center display-4 fw-400">
                            Veggie Foods? Ingredients <br/> You Want To Try</h1>
                      </div>     
                      
                      <div className="d-flex align-self-start justify-content-around mb-5">  
                     <Link to = "/listing">
                      <Button variant="success">
                        <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                         Get Started</p>
                      </Button>{' '}
                      </Link>
                      </div>

                    </div>
                </Row>
            </Container>    
        </div>
    )
}

export default EndBanner
