import React from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';

function Footer() {
    return (
        <div>
           <Container className="mt-5">
               <Row>
                  <Col md={5} sm={12} className="mt-2">
                      <h3 style={{color: '#06C167',}}>FreshlyDropped</h3><br/>
                      <p>Striving to provide good food to our customers.<br/>Since 2021.</p><br/>
                      <p style={{fontSize: "8px"}}>Developed by Joshua Joseph Vaidyan<br/>
                        UI inspired from Figma Community Foodies by Iroshan De Silva<br/>
                        Description given by FakeClients (FreshlyDropped)
                      </p>
                  </Col>

                  <Col md={2} sm={6} className="mt-2 d-flex justify-content-start">
                     <div>
                     <h3>About Us</h3><br/>
                     <div style={{lineHeight: "90%",}}> 
                      <p>About us</p>
                      <p>Service us</p>
                      <p>Contact us</p>
                      <p>Company</p>
                     </div>
                     </div> 
                  </Col>

                  <Col md={2} sm={6} className="mt-2 d-flex justify-content-start">
                  <div>    
                  <h3>Company</h3><br/>
                     <div style={{lineHeight: "90%",}}> 
                      <p>Partnership</p>
                      <p>Terms of Use</p>
                      <p>Privacy</p>
                      <p>Site Map</p>
                     </div>
                  </div>    
                  </Col>

                  <Col md={3} sm={12} className="mt-2 d-flex justify-content-start">
                      <div>
                      <h3>Get in Touch</h3><br/>
                      <div style={{lineHeight: "100%",}}> 
                      <p>Let us contact you by providing a mail id.</p>
                        <Row>

                        <Form>
                        <Form.Group className="mt-3" controlId="formBasicEmail">
                            <Form.Control type="email" placeholder="Enter email" />
                        </Form.Group>
                        </Form>
                       
                        <div className="mt-2">
                        <Button variant="success">
                                      <p  style={{padding:'1px 1px',fontSize: '16px',margin: '1px 1px'}}>
                                      Subscribe</p>
                         </Button>{' '}
                         </div>
                        </Row>
                     </div>
                     </div>
                  </Col>
               </Row> 

               <Row>
                   <div className="d-flex justify-content-center mt-5 mb-5">
                       <p style={{fontSize:"10px",}}>Copyright &copy; 2021 Belongs to respective owners</p>
                   </div>    
               </Row>   
           </Container>  
        </div>
    )
}

export default Footer
