import React,{useState,useEffect} from 'react';
import Container from 'react-bootstrap/Container'; 
import CardComp from './CardComp';
import Row from 'react-bootstrap/Row';
import firebase from "./firebase";

function ProductListingPage() {

    const [proddetails,setproddetails] = useState([]);
    const ref = firebase.firestore().collection('productdetailsdb');
    //console.log(ref)
    function getproductdetails(){
      ref.onSnapshot((Snapshot) =>{
        const items = [];
        Snapshot.forEach((doc)=>{
          items.push(doc.data());
        });
       setproddetails(items);
      });
    }


    useEffect(() => {
      getproductdetails()
    },[]);



    return (
        <div>
          <Container>  
            <Row className="d-flex row mt-4 mb-5">
                 {
                    proddetails.map((product) =>(
                         <CardComp key={product.id} name={product.name} oldrate={product.oldrate} newrate={product.newrate} image={product.image} proute={product.proute} />
                    ))
                 }  
            </Row>           
          </Container>  
        </div>
    )
}

export default ProductListingPage
