import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'; 
import Nav from 'react-bootstrap/Nav';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import './introsection.css';
import {Link} from 'react-router-dom';

function Introsection() {
    const style0 = {
        display: 'flex',
        justifyContent: '',
    }

    const style = {
        color: '#06C167' ,
        textDecoration: 'none',
        fontWeight: 500,
    };

    const style1 = {
        textDecoration: 'none',
        fontWeight: 500,
        margin: '5px 5px',
        padding: '5px'
    };

    const bodystyle={
        fontWeight: 200,
        color: 'grey',
        display: 'flex',
        flexDirection: 'row',
        height:'100%',
    };

    return (
        <div className="introsection">
                    <Container fluid className="Navigation">
                    <Navbar className="d-flex justify-content-between" expand="sm">
                      <div>  
                        <Navbar.Brand href="" style={style}>FreshlyDropped</Navbar.Brand>
                      </div>  
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                      <div>  
                        <Navbar.Collapse>
                        <Nav className="me-auto">
                        <Nav.Link href="" style={style1}>Home</Nav.Link> &nbsp;&nbsp;
                        <Nav.Link href="" style={style1}>About Us</Nav.Link> &nbsp;&nbsp;
                        <Nav.Link href="" style={style1}>Service Us</Nav.Link> &nbsp;&nbsp;
                        <Nav.Link href="" style={style1}>Our Company</Nav.Link> &nbsp;&nbsp;
                        <Nav.Link href="" style={style1}>Pages</Nav.Link> &nbsp;&nbsp;
                        </Nav>
                        </Navbar.Collapse>
                      </div>
                    </Navbar>
                    </Container>

                    <Container fluid className="Introcontent">   
                    <Row className= "">
                        <Col sm={12} md={7} className="col1 pb-3">
                           <div className="div1"> 
                                <div className="h1 we sm-align-self-center">We are Serious<br/>About food you eat</div>
                                <div>We strive to provide the best products organised and ready to cook<br/>
                                    which is chemical free and 100% organic.
                                </div>
                                <div className="mt-4">
                                  <Link to="/listing">
                                   <Button variant="success">
                                      <p  style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                      Get Started</p>
                                   </Button>{' '}
                                   </Link>
                                </div>
                           </div> 
                        </Col>
                        <Col sm={12} style={bodystyle} className="imag d-flex flex-fill sm-mt-4 pb-3" md={5}>
                         <img src="/introimg.png" className="img-fluid mt-4" alt="Responsive image"/>
                        </Col>
                    </Row>
                    </Container>
                    
        </div>
    );
}

export default Introsection
