import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import CardComp from './CardComp';
import './OurProducts.css';

function OurProducts() {
    return (
        <div>
            <Container>
                  <div className="d-flex row pt-5 mt-5 mb-5" style={{lineStyle: '80%',}}>
                      <p>Our Products</p>
                      <h1>Most Popular Products</h1>
                  </div>

                  <Container>
                  <Row className="d-flex row mt-4 mb-5 justify-content-around">
                  
                  <CardComp name="Banana" oldrate="29" newrate="25" image="/p1.png" proute="/listing"/>
                  <CardComp name="Pappaya" oldrate="10" newrate="8" image="/p2.png" proute="/listing"/>
                  <CardComp name="Avocado" oldrate="30" newrate="28" image="/p3.png" proute="/listing"/>
                  <CardComp name="Tangerine" oldrate="18" newrate="16" image="/p4.png" proute="/listing"/>  
                 
                  </Row>                  
                  </Container>
            </Container>    
        </div>
    )
}

export default OurProducts
   

