import React,{ useState } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Toast from 'react-bootstrap/Toast';

function Stars() {
    
    const [showA, setShowA] = useState(false);
    const toggleShowA = () => setShowA(!showA);
    
    return (
        <div>
           <Container> 
                    <Row>
                        <Col sm={12} md={7} className="ps-5 pt-5">
                           <br/><br/><br/>
                           Our Stars<br/><br/>
                            <h1>Veggie food goes on and always on.</h1> <br/><br/>
                            
                          <div style={{lineHeight:'100%',}}>  
                            <div className="d-flex flex-row justify-content-between">
                                <div className="p-2 d-flex row justify-content-center">
                                    <div className="h2" style={{color:'#06C167',}}>1,500</div>
                                    <p>Sold Product</p>
                                </div>
                                <div className="p-2 d-flex row justify-content-center">
                                    <div className="h2" style={{color:'#06C167',}}>500</div>
                                    <p>Positive Feedback</p>
                                </div>
                                <div className="p-2 d-flex row justify-content-center">
                                    <div className="h2" style={{color:'#06C167',}}>400</div>
                                    <p>Official Store</p>
                                </div>
                            </div>
                          </div> 

                          <div className="mt-4">
                                   <Button variant="success"  onClick={toggleShowA}>
                                       <p style={{padding:'3px 3px',fontSize: '20px',margin: 'auto auto'}}>
                                       Get Special Promo</p>
                                   </Button>{' '}


                                   <Toast show={showA} onClose={toggleShowA} position="top-end">
                                        <Toast.Header>
                                            Your Promo code
                                        </Toast.Header>
                                        <Toast.Body>FreshlyDropped@New</Toast.Body>
                                    </Toast>
                          </div> 
                        </Col>
                        <Col sm={12} md={5} className="pt-5"> 
                            <br/><br/><br/>
                            <img src="/drink.png" alt="benefit" className="img-fluid"/>
                        </Col>
                    </Row>
           </Container> 
            
        </div>
    )
}

export default Stars
